* {
  font-family: "Open Sans";
  font-weight: 100;
}

body {
  width: 100%;
  margin: 0 auto;
}

header {
  /*background-image: url("../images/background-3.jpg"); */
  background: linear-gradient(200deg, #226d99 25%, #55ee77 100%);
  background-size: 150% 150%;
  background-repeat: no-repeat;
  width: 100%;
  height: 300px;
  margin: 0 auto;
  animation: gradient-animation 7s linear infinite;
}

@media screen and (max-width: 716px) {
  header {
    height: 400px;
  }
}

@keyframes gradient-animation {
  0% {
    background-position: 30% 70%;
  }
  50% {
    background-position: 90% 10%;
  }
  100% {
    background-position: 30% 70%;
  }
}

header .info {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 300px 300px 300px;
  justify-items: center;
  align-items: center;
  color: #333;
}

@media screen and (max-width: 716px) {
  header .info {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 716px) {
  header .info .contacts {
    display: none;
  }
}

header .info .contacts .mail a,
header .info .contacts .phone a {
  text-decoration: none;
  color: #333;
  font-size: 20px;
}

@media screen and (max-width: 800px) {
  header .info .contacts .mail a,
  header .info .contacts .phone a {
    font-size: 16px;
  }
}

header .info .contacts .mail a:hover,
header .info .contacts .phone a:hover {
  color: black;
  text-shadow: 0 0 1px #262626;
}

header .info .photo {
  text-align: center;
  line-height: 0.5rem;
  margin-top: 2rem;
}

header .info .photo .github-hide {
  display: none;
  margin: 0 0 1rem 0;
}

header .info .photo .github-hide #GitHub-Hide {
  width: 80px;
  height: 25px;
  margin: 0;
}

header .info .photo .github-hide #GitHub-Hide:hover {
  fill: #6e5494;
}

@media screen and (max-width: 716px) {
  header .info .photo .github-hide {
    display: block;
  }
}

header .info .photo .phone {
  margin: 1.5rem 0 -3rem 0;
}

header .info .photo .mail {
  margin: 1.5rem 0 0 0;
}

header .info .photo .mail,
header .info .photo .phone {
  display: none;
}

header .info .photo .mail a,
header .info .photo .phone a {
  text-decoration: none;
  color: #333;
  font-size: 20px;
}

@media screen and (max-width: 716px) {
  header .info .photo .mail a,
  header .info .photo .phone a {
    font-size: 16px;
  }
}

header .info .photo .mail a:hover,
header .info .photo .phone a:hover {
  color: black;
  text-shadow: 0 0 1px #262626;
}

@media screen and (max-width: 716px) {
  header .info .photo .mail,
  header .info .photo .phone {
    display: block;
  }
}

header .info .photo img {
  width: 120px;
  border-radius: 120px;
  border: 1px solid #999;
}

header .info .photo h4 {
  font-size: 1rem;
}

header .info .photo ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: center;
  align-items: center;
  padding: 0;
}

header .info .photo ul li {
  list-style-type: none;
  padding: 0.5rem;
}

header .info .photo ul #LinkedIn {
  width: 40px;
  height: 40px;
}

header .info .photo ul #LinkedIn:hover {
  fill: #0077B5;
}

@media screen and (max-width: 716px) {
  header .info .photo ul #LinkedIn {
    width: 25px;
  }
}

header .info .photo ul #Facebook {
  width: 40px;
  height: 40px;
}

header .info .photo ul #Facebook:hover {
  fill: #4267b2;
}

@media screen and (max-width: 716px) {
  header .info .photo ul #Facebook {
    width: 25px;
  }
}

header .info .photo ul #Instagram {
  width: 40px;
  height: 40px;
}

header .info .photo ul #Instagram:hover {
  fill: url(#MyGradient);
}

@media screen and (max-width: 716px) {
  header .info .photo ul #Instagram {
    width: 27px;
  }
}

header .info .photo ul #Youtube {
  width: 45px;
  height: 45px;
}

header .info .photo ul #Youtube:hover {
  fill: #ff0000;
}

@media screen and (max-width: 716px) {
  header .info .photo ul #Youtube {
    width: 30px;
  }
}

@media screen and (max-width: 716px) {
  header .info .photo ul {
    margin: -1rem 0 0rem 0;
  }
}

header .social h2 {
  text-align: center;
}

header .portfolio h2, header .portfolio h3 {
  text-align: center;
}

header .portfolio #GitHub {
  width: 200px;
  height: 60px;
}

header .portfolio #GitHub:hover {
  fill: #6e5494;
}

@media screen and (max-width: 800px) {
  header .portfolio #GitHub {
    width: 130px;
  }
}

@media screen and (max-width: 716px) {
  header .portfolio h2, header .portfolio h3, header .portfolio #GitHub {
    display: none;
  }
}

main {
  max-width: 90%;
  margin: 0 auto;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 3rem 3rem;
  margin: 0 auto;
  margin-top: 3rem;
}

.container .hello,
.container .education {
  border: 1px solid #ccc;
  padding: 1rem 2rem;
}

.container .education ul li #date {
  font-size: 14px;
  margin-top: -1rem;
  color: #999;
}

@media screen and (max-width: 800px) {
  .container .education h2 {
    text-align: center;
  }
  .container .education ul {
    padding: 0;
    list-style-type: none;
  }
}

@media screen and (max-width: 800px) {
  .container {
    grid-template-columns: 1fr;
  }
}

.skills {
  border: 1px solid #ccc;
  padding: 1rem 2rem;
}

.skills ul {
  line-height: 2rem;
  padding: 1rem;
}

.skills ul .elements {
  display: grid;
  max-width: 100%;
  grid-template-columns: repeat(auto-fill, 75px);
  justify-items: center;
  align-items: center;
}

.skills ul .elements .image {
  margin: 0 0 1rem 0;
  position: relative;
}

.skills ul .elements .image img {
  height: 40px;
  width: 40px;
}

.skills ul .git img {
  width: 55px !important;
  padding-top: 1.3rem;
}

.skills ul .css3 img {
  width: 80px !important;
}

.experience {
  border: 1px solid #ccc;
  padding: 1rem 2rem;
}

.experience .main-ul li a {
  text-decoration: none;
  color: #6e5494;
}

.experience .main-ul h3 a {
  text-decoration: none;
  color: #6e5494;
  font-weight: 400;
}

.experience .main-ul h3 a:hover {
  color: #564273;
  text-shadow: 0 0 2px #bdafd2;
}

.experience .main-ul .dates-work {
  font-size: 14px;
  margin-top: -1rem;
  color: #999;
}

.experience .main-ul .position {
  margin-top: -0.7rem;
}

.experience .main-ul hr {
  display: none;
  color: #ccc;
  height: 1px;
  width: 50%;
  margin: 1rem auto;
  background-color: #ccc;
  border: none;
}

@media screen and (max-width: 800px) {
  .experience .main-ul hr {
    display: block;
  }
}

@media screen and (max-width: 1148px) {
  .experience .main-ul #line-1:before,
  .experience .main-ul #line-2:before,
  .experience .main-ul #line-3:before {
    display: none;
  }
}

@media screen and (max-width: 800px) {
  .experience .main-ul #line-1:before,
  .experience .main-ul #line-2:before,
  .experience .main-ul #line-3:before {
    display: none;
  }
}

@media screen and (max-width: 800px) {
  .experience h2 {
    text-align: center;
  }
  .experience ul {
    padding: 0;
    list-style-type: none;
  }
}

.full-container {
  margin: 3rem auto;
  border: 1px solid #ccc;
  padding: 1rem 2rem;
}

.full-container .hobbies ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  list-style-type: none;
  justify-items: center;
  align-items: center;
  text-align: center;
  padding: 0;
}

.full-container .hobbies ul li .interes {
  width: 100px;
  height: 100px;
  border: 1px solid #d9b38c;
  border-radius: 100px;
}

.full-container .hobbies ul li .interes img {
  width: 60px;
  height: 60px;
  margin: 1.25rem 0;
}

@media screen and (max-width: 800px) {
  .full-container .hobbies ul {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 480px) {
  .full-container .hobbies ul {
    grid-template-columns: 1fr;
    text-align: center;
  }
}

@media screen and (max-width: 480px) {
  .full-container .hobbies {
    text-align: center;
  }
}

.markups ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  list-style-type: none;
  text-align: center;
  padding: 2rem 0;
  color: #6e5494;
}

.markups ul a {
  text-decoration: none;
  color: #6e5494;
}

.markups ul a img {
  width: 90%;
  margin-bottom: -1rem;
  border: 1px solid #ccc;
}

.markups ul a:hover {
  color: #564273;
  text-shadow: 0 0 2px #bdafd2;
}

.markups ul a:hover img {
  transform: scale(1.01);
}

@media screen and (max-width: 800px) {
  .markups ul {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 800px) {
  .markups h2 {
    text-align: center;
  }
}

footer {
  height: 60px;
  background-color: #333;
  text-align: center;
}

footer h2 {
  padding: 0.3rem;
  margin: 0;
}

footer h2 a {
  color: #ccc;
  text-decoration: none;
  margin: 1rem;
  font-size: 36px;
}

footer h2 a:hover {
  color: #fff;
  transition: 0.5s;
}

footer .overlay {
  background-color: rgba(0, 0, 0, 0.8);
  bottom: 0;
  cursor: default;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  z-index: 99999;
  -webkit-transition: opacity .5s;
  -moz-transition: opacity .5s;
  -ms-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
}

footer .popup {
  background-color: #eee;
  display: inline-block;
  left: 50%;
  opacity: 0.6;
  padding: 15px;
  width: 30rem;
  height: 22rem;
  position: fixed;
  top: 40%;
  visibility: hidden;
  z-index: 999999;
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: opacity .5s, top .5s;
  -moz-transition: opacity .5s, top .5s;
  -ms-transition: opacity .5s, top .5s;
  -o-transition: opacity .5s, top .5s;
  transition: opacity .5s, top .5s;
  border-radius: 5px;
}

footer .popup h3 {
  font-size: 25px;
}

footer .popup .close_window {
  position: absolute;
  right: 10px;
  top: 3px;
  cursor: pointer;
  color: #333;
  font-family: 'tahoma', sans-serif;
  text-align: center;
}

footer .popup .close_window:hover {
  transform: scale(1.3);
}

footer .popup .close_window:active {
  transform: scale(0.9);
}

footer #sobrancelhas {
  transform: translateY(-9px);
  animation: sobrancelhas 5s linear infinite;
}

@keyframes sobrancelhas {
  0% {
    transform: translateY(-10);
    -ms-transform: translateY(-10);
    -moz-transform: translateY(-10);
  }
  25% {
    transform: translateY(0px);
    -ms-transform: translateY(0px);
    -moz-transform: translateY(0px);
  }
  75% {
    transform: translateY(0px);
    -moz-transform: translateY(0px);
    -ms-transform: translateY(0px);
  }
  100% {
    transform: translateY(10);
    -moz-transform: translateY(10);
    -ms-transform: translateY(10);
  }
}

footer #nariz {
  transform: translateY(-9px);
  -ms-transform: translateY(-9px);
  -moz-transform: translateY(-9px);
  animation: nariz 5s linear infinite;
}

@keyframes nariz {
  0% {
    transform: translateY(-10);
